.title {
	font-family: "Bebas Neue", cursive;
	color: #d7171e;
	font-size: 100px;
	font-weight: 500;
	animation-name: fadeIn;
	animation-duration: 2s;
	height: 15vw;
	width: 45vw;
	max-height: 105px;
	min-width: 700px;
	text-align: center;
}

.text {
	font-family: "DM Sans", sans-serif;
	color: white;
	cursor: pointer;
}

.category_name {
	font-family: "DM Sans", sans-serif;
	color: white;
	text-shadow: 3px 3px 4px #000;
}

.play_button {
	cursor: pointer;
}

.poster_name {
	font-family: "Bebas Neue", cursive;
	color: white;
	text-shadow: 5px 5px 5px #000;
}

.poster_description {
	font-family: "DM Sans", sans-serif;
	color: white;
	text-shadow: 1px 1px 5px #000;
}

.imageAnimation {
	height: 20vw;
	width: 60vw;
	max-height: 225px;
	max-width: 600px;
	resize: inherit;
	animation-name: fadeInImage;
	animation-duration: 3s;
}

.authImage {
	background-image: url("https://cdn.theatlantic.com/thumbor/m_UsuD0rJefiTXLcLuzo8DqaYc4=/4x289:2607x1645/960x500/media/img/mt/2020/03/GettyImages_120685930/original.jpg");
	background-size: cover;
}

.filter {
	background-image: linear-gradient(
		to right,
		rgba(0, 0, 0, 0.8),
		rgba(0, 0, 0, 0)
	);
}

.tab_filter {
	background-image: linear-gradient(
		to bottom,
		rgba(0, 0, 0, 0.8),
		rgba(0, 0, 0, 0)
	);
}

.home_container {
	display: flex;
	flex-direction: column;
	background-color: #141414;
	flex: 1;
	width: 100%;
	/* overflow-x: hidden;	 */
}

.poster_fade {
	width: 100%;
	background-image: linear-gradient(
		to top,
		#141414,
		#141414cf,
		#14141466,
		#14141422,
		rgba(0, 0, 0, 0)
	);
	height: 300px;
	position: absolute;
	z-index: 5;
	/* margin-top: -100; */
	/* background-color: red; */
}

.poster {
	width: 100%;
	object-fit: contain;
	/* min-height: 650px;
  min-width: 1150;
  overflow: hidden; */
}

.category_poster {
	object-fit: contain;
	width: 300px;
}

.movie_block {
	transition: 500ms;
	display: flex;
	flex-direction: column;
	/* position: absolute; */
}

.movie_block:hover {
	transform: scale(1.5);
	box-shadow: 10px;
	/* position: relative; */
}

.category_poster {
	border-radius: 5px;
	/* z-index: 100; */
}

.category_div {
	overflow: scroll;
	overflow-y: hidden;
}

.category_div::-webkit-scrollbar {
	display: none;
}

.detail_parent_div::-webkit-scrollbar {
	display: none;
}

