.detail_movie_poster {
	width: 100%;
	object-fit: contain;
	z-index: 20;
}

.detail_poster {
	object-fit: contain;
}

.detail_poster_fade {
	width: 100%;
	background-image: linear-gradient(
		to top,
		#141414,
		#141414cf,
		#14141466,
		#14141422,
		rgba(255, 0, 0, 0)
	);
	/* background-color: red; */
	/* height: 200px; */
	/* position: absolute; */
	z-index: 5;
	max-width: 850px;
	/* top: 310px; */
}

@keyframes fadeIn {
	from {
		scale: 0;
		opacity: 0;
	}
	to {
		scale: 1;
		opacity: 1;
	}
}

@keyframes fadeInImage {
	0% {
		transform: scale(0);
		opacity: 0;
	}
	50% {
		transform: scale(1.2);
		opacity: 0.8;
	}
	100% {
		transform: scale(1);
		opacity: 1;
	}
}
